import forEach from 'lodash/forEach';
import isArray from 'lodash/isArray';
import { isAbsoluteUrl } from './util';
import { ERRORS } from './constants';
export function createPathRewriter(rewriteConfig) {
    if (!isValidRewriteConfig(rewriteConfig)) {
        return;
    }
    return function (hostAndPath) {
        var path = getPath(hostAndPath);
        var result = path;
        var validResult = false;
        forEach(rewriteConfig, function (rule) {
            if (rule.regex.test(path)) {
                validResult = true;
                result = result.replace(rule.regex, rule.value);
                return false;
            }
        });
        if (!validResult) {
            return false;
        }
        return result;
    };
}
export function isValidRewriteConfig(rewriteConfig) {
    if (isArray(rewriteConfig)) {
        return true;
    }
    else {
        throw new Error(ERRORS.ERR_PATH_REWRITER_CONFIG);
    }
}
export function getPath(url) {
    var path;
    if (window.URL) {
        if (isAbsoluteUrl(url)) {
            path = new URL(url).pathname;
        }
        else {
            path = new URL(url, location.href).pathname;
        }
    }
    else {
        var link = document.createElement('a');
        link.href = url;
        path = link.pathname;
        link = null;
    }
    return path;
}
